

import { Group, Vector3, Quaternion, Clock } from 'three';

import SimpleTetrahedron from './SimpleTetrahedron';
import SimpleOctahedron from './SimpleOctahedron';

const gap = 0.01;
const size = [1.5, 1.0, 0.8, 0.5, 0.3, 0.25];

const squareRootOf3 = 1 / Math.sqrt(3);
const possibleRotatingAxises = [
  new Vector3(+squareRootOf3, +squareRootOf3, +squareRootOf3),
  new Vector3(-squareRootOf3, -squareRootOf3, +squareRootOf3),
  new Vector3(-squareRootOf3, +squareRootOf3, -squareRootOf3),
  new Vector3(+squareRootOf3, -squareRootOf3, -squareRootOf3)
];

function getRotatingQuaternion (rotatingAxis) {
  var quaternion = new Quaternion();
  quaternion.setFromAxisAngle( rotatingAxis, Math.PI * 2 / 3 );
  return quaternion;
}
const possibleRotatingQuaternions = possibleRotatingAxises.map(getRotatingQuaternion);
const startQuaternion = new Quaternion();
const clock = new Clock();


class MagicTetrahedron extends Group {

  constructor (numberOfRows) {
    super();
    this.numberOfRows = numberOfRows;

    const distance = size[numberOfRows-1] / Math.sqrt(8) + gap;
    const startPosition = (numberOfRows - 1) * distance;

    const position = new Vector3(startPosition, startPosition, startPosition);

    this.allTetrahedrons = new Array(numberOfRows);
    for (let i = 0; i < numberOfRows; i++) {
      this.allTetrahedrons[i] = new Array(i+1);
      const rowOffset = i * 2 * distance;
      const rowPosition = new Vector3().addVectors(position, new Vector3(-rowOffset, 0, -rowOffset));
      for (let j = 0; j < i+1; j++) {
        this.allTetrahedrons[i][j] = new Array(j+1);
        const columnOffset = j * 2 * distance;
        const columnPosition = new Vector3().addVectors(rowPosition, new Vector3(0, -columnOffset, +columnOffset));
        for (let k = 0; k < j+1; k++) {
          const elementOffset = k * 2 * distance;
          const elementPosition = new Vector3().addVectors(columnPosition, new Vector3(+elementOffset, 0, -elementOffset));
          const simpleTetrahedron = new SimpleTetrahedron(size[numberOfRows - 1]);
          simpleTetrahedron.name = `SimpleTetrahedron (${i}, ${j}, ${k})`;
          simpleTetrahedron.position.copy(elementPosition);
          this.allTetrahedrons[i][j][k] = simpleTetrahedron;
          // this.add(simpleTetrahedron);
        }
      }
    }

    position.add(new Vector3(+distance, -distance, +distance));

    this.allOctahedrons = new Array(numberOfRows);
    for (let i = 0; i < numberOfRows; i++) {
      this.allOctahedrons[i] = new Array(i);
      const rowOffset = i * 2 * distance;
      const rowPosition = new Vector3().addVectors(position, new Vector3(-rowOffset, 0, -rowOffset));
      for (let j = 0; j < i; j++) {
        this.allOctahedrons[i][j] = new Array(j+1);
        const columnOffset = j * 2 * distance;
        const columnPosition = new Vector3().addVectors(rowPosition, new Vector3(0, -columnOffset, +columnOffset));
        for (let k = 0; k < j+1; k++) {
          const elementOffset = k * 2 * distance;
          const elementPosition = new Vector3().addVectors(columnPosition, new Vector3(+elementOffset, 0, -elementOffset));
          const simpleOctahedron = new SimpleOctahedron(size[numberOfRows - 1]);
          simpleOctahedron.name = `SimpleOctahedron (${i}, ${j}, ${k})`;
          simpleOctahedron.position.copy(elementPosition);
          this.allOctahedrons[i][j][k] = simpleOctahedron;
          // this.add(simpleOctahedron);
        }
      }
    }
    this.selectedRow = 1;
    this.selectedAxis = 0;
    this.assembleLayers();
  }

  selectNextRotation () {
    this.selectedAxis = (this.selectedAxis + 1) % 4;
    // this.selectedAxis = Math.floor(Math.random() * 4);
    // this.selectedRow = Math.floor(Math.random() * (this.allTetrahedrons.length - 1));
    // rotatingAngle *= (2 * random.nextInt(2) - 1);
  }

  assembleLayers () {
    for (let i = 0; i < this.numberOfRows; i++) {
      const group = new Group();
      group.name = `Layer (${i})`;
      const m = this.numberOfRows - 1 - i;
      for (let j = 0; j < this.allTetrahedrons[i].length; j++) {
        for (let k = 0; k < this.allTetrahedrons[i][j].length; k++) {
          switch (this.selectedAxis) {
            case 0:
                group.add(this.allTetrahedrons[i][j][k]);
                break;
            case 1:
                group.add(this.allTetrahedrons[m+j][m+k][k]);
                break;
            case 2:
                group.add(this.allTetrahedrons[m+j][j][j-k]);
                break;
            case 3:
                group.add(this.allTetrahedrons[m+j][m+j-k][m]);
                break;
            default:
                throw new IllegalArgumentException("unknown axis " + this.selectedAxis);
          }
        }
      }
      for (let j = 0; j < this.allOctahedrons[i].length; j++) {
        for (let k = 0; k < this.allOctahedrons[i][j].length; k++) {
          switch (this.selectedAxis) {
            case 0:
                group.add(this.allOctahedrons[i][j][k]);
                break;
            case 1:
                group.add(this.allOctahedrons[m+j+1][m+k][k]);
                break;
            case 2:
                group.add(this.allOctahedrons[m+j+1][j][j-k]);
                break;
            case 3:
                group.add(this.allOctahedrons[m+j+1][m+j-k][m]);
                break;
            default:
                throw new IllegalArgumentException("unknown axis " + this.selectedAxis);
          }
        }
      }
      this.add(group);
    }
  }

  assembleLayersByAxis0 () {
    for (let i = 0; i < this.numberOfRows; i++) {
      const group = new Group();
      for (let j = 0; j < i+1; j++) {
        for (let k = 0; k < j+1; k++) {
          group.add(this.allTetrahedrons[i][j][k]);
        }
      }
      this.add(group);
    }
  }

  handleSelectedObjects (intersectedObjects) {
    if (intersectedObjects.length > 0) {
      const { object, faceIndex } = intersectedObjects[0];
      console.log(object.name, faceIndex);
    }
  }

  startRotating () {
    if (!clock.running) {
      this.selectNextRotation();
      this.remove(...this.children);
      this.assembleLayers();
      clock.start();
    }
  }

  update (timeStamp) {
    this.rotation.y = timeStamp / 10000;
    const rotatingLayer = this.children[this.selectedRow];
    // this.children[1].rotateOnAxis(possibleRotatingAxises[this.selectedAxis], 0.001);
    if (clock.running) {
      const duration = 1;
      if (clock.getElapsedTime() < duration) {
        Quaternion.slerp( startQuaternion, possibleRotatingQuaternions[this.selectedAxis], rotatingLayer.quaternion, clock.getElapsedTime() / duration );
      } else {
        clock.stop();
        this.rotateColors(rotatingLayer);
        rotatingLayer.quaternion.copy(startQuaternion);
      }
    }
  }

  rotateColors (rotatingLayer) {
    const trans = [...getIndexes(this.selectedRow + 1), ...getIndexes(this.selectedRow, (this.selectedRow + 1) * (this.selectedRow + 2) / 2)];
    console.log(this.selectedRow, getIndexes(this.selectedRow + 1), getIndexes(this.selectedRow), trans);
    trans.map(i => rotatingLayer.children[i].getHexs()).forEach((hexs, i) => { rotatingLayer.children[i].setHexs(hexs); });
    for (const tetrahedron of rotatingLayer.children) {
      tetrahedron.rotateColors(this.selectedAxis);
    }
  }

}


export default MagicTetrahedron;


function getIndexes (size, offset = 0) {
  switch (size) {
    case 1: return [0].map(i => i + offset);
    case 2: return [2, 0, 1].map(i => i + offset);
    case 3: return [5, 2, 4, 0, 1, 3].map(i => i + offset);
    default: return [];
  }
}
