

import { Face3, FaceColors, Geometry, Mesh, MeshPhongMaterial, Vector3 } from 'three';

import SimpleColor from './SimpleColor';


class SimpleTetrahedron extends Mesh {

  constructor (size) {
    const delta = size / Math.sqrt(8);

    // create a triangular geometry
    const geometry = new Geometry();

    geometry.vertices.push(
      new Vector3( +delta, +delta, +delta ),
      new Vector3( -delta, -delta, +delta ),
      new Vector3( -delta, +delta, -delta ),
      new Vector3( +delta, -delta, -delta ),
    );

    geometry.colors.push(
      // SimpleColor.RED,
      // SimpleColor.BLUE,
      // SimpleColor.GREEN,
      // SimpleColor.YELLOW,
    );

    // add the face to the geometry's faces array
    geometry.faces.push(
      new Face3( 2, 3, 1, null, SimpleColor.RED.clone() ),
      new Face3( 3, 2, 0, null, SimpleColor.BLUE.clone() ),
      new Face3( 1, 3, 0, null, SimpleColor.GREEN.clone() ),
      new Face3( 2, 1, 0, null, SimpleColor.YELLOW.clone() ),
    );

    // the face normals and vertex normals can be calculated automatically if not supplied above
    geometry.computeFaceNormals();
    // geometry.computeVertexNormals();

    const materialParameters = {
      vertexColors: FaceColors,
      // side: THREE.DoubleSide, // For a solid shape like a sphere or a cube there's usually no reason to draw the back sides of triangles as they all face inside the shape.
    };
    super( geometry, new MeshPhongMaterial( materialParameters ) );
  }

  update (timeStamp) {
    this.rotation.x = timeStamp / 10000;
    this.rotation.y = timeStamp / 10000;
  }

  getHexs () {
    return this.geometry.faces.map(face => face.color.getHex());
  }

  setHexs (hexs) {
    hexs.forEach((hex, i) => { this.geometry.faces[i].color.setHex(hex); });
    this.geometry.colorsNeedUpdate = true;
  }

  rotateColors (rotatingAxis) {
    function colorToString (color) {
      switch (color.getHex()) {
        case SimpleColor.RED.getHex(): return 'RED';
        case SimpleColor.BLUE.getHex(): return 'BLUE';
        case SimpleColor.GREEN.getHex(): return 'GREEN';
        case SimpleColor.YELLOW.getHex(): return 'YELLOW';
      }
    }
    const before = this.geometry.faces.map(face => colorToString(face.color)).join(', ');
    switch (rotatingAxis) {
      case 0:
        [0, 2, 3, 1].map((i) => this.geometry.faces[i].color.getHex()).forEach((c, i) => { this.geometry.faces[i].color.setHex(c); });
        break;
      case 1:
        [3, 1, 0, 2].map((i) => this.geometry.faces[i].color.getHex()).forEach((c, i) => { this.geometry.faces[i].color.setHex(c); });
        break;
      case 2:
        [1, 3, 2, 0].map((i) => this.geometry.faces[i].color.getHex()).forEach((c, i) => { this.geometry.faces[i].color.setHex(c); });
        break;
      case 3:
        [2, 0, 1, 3].map((i) => this.geometry.faces[i].color.getHex()).forEach((c, i) => { this.geometry.faces[i].color.setHex(c); });
        break;
      default:
        throw new Error(`Unknown axis ${rotatingAxis}`);
    }
    this.geometry.colorsNeedUpdate = true;
    const after = this.geometry.faces.map(face => colorToString(face.color)).join(', ');
    console.log(`rotatingAxis = ${rotatingAxis}, [${before}] => [${after}]`);
  }

}


export default SimpleTetrahedron;
