/**
 * entry.js
 *
 * This is the first file loaded. It sets up the Renderer,
 * Scene and Camera. It also starts the render loop and
 * handles window resizes.
 *
 */

import { DirectionalLight, WebGLRenderer, PerspectiveCamera, Scene, Vector3 } from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';

// import SeedScene from './objects/Scene.js';
import { MagicTetrahedron } from './objects/tetrahedron';

const scene = new Scene();
const camera = new PerspectiveCamera();
const renderer = new WebGLRenderer({ antialias: true });
// const seedScene = new SeedScene();
const seedScene = new MagicTetrahedron(3);

// scene
scene.add(seedScene);

// camera
camera.position.set(0, 2 , 2 * Math.sqrt(3));
//camera.position.set(6,3,-10);
camera.lookAt(new Vector3(0,0,0));

// renderer
renderer.setPixelRatio(window.devicePixelRatio);
renderer.setClearColor(0x7ec0ee, 1);

{
  const color = 0xFFFFFF;
  const intensity = 1;
  const light = new DirectionalLight(color, intensity);
  light.position.set(-1, 2, 4);
  scene.add(light);
}


import PickHelper from './PickHelper';
const pickHelper = new PickHelper();

const pickPosition = {x: 0, y: 0};
clearPickPosition();

function getCanvasRelativePosition(event) {
  const canvas = renderer.domElement;
  const rect = canvas.getBoundingClientRect();
  return {
    x: (event.clientX - rect.left) * canvas.width  / rect.width,
    y: (event.clientY - rect.top ) * canvas.height / rect.height,
  };
}

function setPickPosition(event) {
  const canvas = renderer.domElement;
  const pos = getCanvasRelativePosition(event);
  pickPosition.x = (pos.x / canvas.width ) *  2 - 1;
  pickPosition.y = (pos.y / canvas.height) * -2 + 1;  // note we flip Y
}

function clearPickPosition() {
  // unlike the mouse which always has a position
  // if the user stops touching the screen we want
  // to stop picking. For now we just pick a value
  // unlikely to pick something
  pickPosition.x = -100000;
  pickPosition.y = -100000;
}

window.addEventListener('mousemove', setPickPosition);
window.addEventListener('mouseout', clearPickPosition);
window.addEventListener('mouseleave', clearPickPosition);


// render loop
const onAnimationFrameHandler = (timeStamp) => {

  pickHelper.pick(pickPosition, scene, camera, timeStamp / 1000);

  renderer.render(scene, camera);
  seedScene.update && seedScene.update(timeStamp);
  window.requestAnimationFrame(onAnimationFrameHandler);
}
window.requestAnimationFrame(onAnimationFrameHandler);

// resize
const windowResizeHanlder = () => {
  const { innerHeight, innerWidth } = window;
  renderer.setSize(innerWidth, innerHeight);
  camera.aspect = innerWidth / innerHeight;
  camera.updateProjectionMatrix();
};
windowResizeHanlder();
window.addEventListener('resize', windowResizeHanlder);


import { Raycaster } from 'three';
const raycaster = new Raycaster();

const onClichHandler = (event) => {
  const rect = renderer.domElement.getBoundingClientRect();
  const x = (event.clientX - rect.left) / rect.width * 2 - 1;
  const y = (rect.top - event.clientY) / rect.height * 2 + 1;
  // cast a ray through the frustum
  raycaster.setFromCamera({x, y}, camera);
  // get the list of objects the ray intersected
  const intersectedObjects = raycaster.intersectObjects(seedScene.children, true);
  seedScene.handleSelectedObjects(intersectedObjects);
}
window.addEventListener('click', onClichHandler);

const onDblClichHandler = (event) => {
  seedScene.startRotating();
};
window.addEventListener('dblclick', onDblClichHandler);

// dom
document.body.style.margin = 0;
document.body.appendChild( renderer.domElement );


// Add Orbit Controls
const orbitControls = new OrbitControls(camera, renderer.domElement);
orbitControls.target.set(0, 0, 0);
orbitControls.update();
